import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import Spinning from 'grommet/components/icons/Spinning';


 class DonorsMap extends Component {
   constructor(props){
     super(props);
     this.state={
       donors:this.props.donors,
       mark:[]
     }
   }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.google !== this.props.google || prevProps.donors !== this.props.donors) {
      this.loadMap();
    }
  }

  componentWillReceiveProps(nextProps){
        if(nextProps.donors !== this.props.donors){
            this.setState({donors:nextProps.donors});
        }
    }

  loadMap() {
      if (this.props && this.props.google) {
        const {google} = this.props;
        const maps = google.maps;
        const mapRef = this.refs.map;
        const node = ReactDOM.findDOMNode(mapRef);
        // const infowindow = new maps.InfoWindow();
        const bounds = new maps.LatLngBounds({lat: Number(40.7485722), lng: Number(-74.0068633)});

        const mapConfig = Object.assign({}, {
          center: bounds,
          zoom: 11,
          scaleControl: true,
          mapTypeId: 'roadmap'
        })


        this.map = new maps.Map(node, mapConfig);

        this.props.donors.map( donor => {
          //Set location for bound area and zoom
          const loc = new maps.LatLng(Number(donor.lat), Number(donor.long) );
            bounds.extend(loc);
            //Marker position
            const infowindow = new google.maps.InfoWindow({

              content:'<p><b>Uluru</b>, also referred to as <b>Ayers Rock</b></p>'
          });
          const marker =new maps.Marker({
            map: this.map,
          //  position: {lat:Number(donor.lat), lng:Number(donor.long) },
            position: loc,
            label: `${donor.id}`,
            title: 'Uluru (Ayers Rock)'
          });

            this.map.fitBounds(bounds);
            this.map.panToBounds(bounds);



          marker.addListener('click', () => {
            // window.location.href = donor.url
          // infowindow.setContent(`${marker.label}`)
          infowindow.open(this.map, marker);
          infowindow.getContent(marker.content)
          console.log('marker', marker,infowindow);
        });
        console.log('marker', marker.label, infowindow);
        this.setState({
          mark:marker
        })
      })
    }
  }


  render() {
    console.log(this.location);
    const style = {
      width:'30vw',
      height:'60vh',
      backgroundColor:'#0a64a0',
      margin:'auto',
      padding:'10rem',
    }
    console.log(this.props.google.maps);
    return (
      <div ref="map" style={style}>
          <Spinning
          justify='center'
          align='center'
          size='xlarge' />
      </div>
    )
  }
}

export default DonorsMap;



