import React, {Component} from 'react';
import './App.css';
import {Redirect, Link} from 'react-router-dom';


class Page404 extends Component{
  constructor(){
    super()
    this.state={
      time:0,
      home:false
    }
  }

  componentDidMount(){
    setTimeout(()=> {

        this.setState({
          time: this.state.time+=1,
          home: true
        })
    }, 4000)
  }

  render(){
    if(this.state.home){
      return   <Redirect to={"/"} />
    }


    return (
      <div className='unknown'>
        <p>This page does not exist, you will be redirected...</p>
        <p><Link to='/'>Home</Link></p>
      </div>
      )
  }
}

export default Page404;



